

.top-pane {
    background-color: hsl(225, 6%, 25%);
  }
  
  .pane {
    height: 50vh;
    display: flex;
  }

  @media(max-width:576px){
    .pane {
        height: 100vh;
        display: grid;
      }
  }
  
  .editor-container {
    flex-grow: 1;
    flex-basis: 0;
    display: flex;
    flex-direction: column;
    padding: .5rem;
    background-color: hsl(225, 6%, 25%);
  }
  
  .editor-container.collapsed {
    flex-grow: 0;
  }
  
  .editor-container.collapsed .CodeMirror-scroll {
    position: absolute;
    overflow: hidden !important;
  }
  
  .expand-collapse-btn {
    margin-left: .5rem;
    background: none;
    border: none;
    color: white;
    cursor: pointer;
  }
  
  .editor-title {
    display: flex;
    justify-content: space-between;
    background-color: hsl(225, 6%, 13%);
    color: white;
    padding: .5rem .5rem .5rem 1rem;
    border-top-right-radius: .5rem;
    border-top-left-radius: .5rem;
  }
  
  .CodeMirror {
    height: 100% !important;
    font-family: Inter;
  }
  
  .code-mirror-wrapper {
    flex-grow: 1;
    border-bottom-right-radius: .5rem;
    border-bottom-left-radius: .5rem;
    overflow: hidden;
  }

  .html {
    display: block;
    width: 32px;
    height: 32px;
    background-image: url(../assets/html.png) !important;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.css {
    display: block;
    width: 32px;
    height: 32px;
    background-image: url(../assets/css.png) !important;
    background-repeat: no-repeat;
    background-size: 100% 100%;

}

.javascript {
    display: block;
    width: 50px;
    height: 32px;
    background-image: url(../assets/js.png) !important;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}